import Vue from 'vue'
import VueRouter from 'vue-router'

import Home from '../views/Home.vue'
import About from '../views/About.vue'
import NewOne from '../views/NewOne.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/about',
    name: 'About',
    component: About
  },
  {
    path: '/newOne',
    name: 'NewOne',
    component: NewOne
  }
]

const router = new VueRouter({
  routes
})

export default router
